.mask{
    width: 100%;
    height: 100%;
    position: relative;

}
.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.mask::after{
    content:"";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;;
    opacity: 0.4;
}
.hero{
    height: 100%;
    width: 100%;


}
.hero .content{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;

}
    
.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 0;
}


.hero .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
    padding-bottom: 2rem;
}
.content .btn{
    margin: 1rem 0.2rem;
}
@media screen and (max-width: 980px){
    .hero .content{
        top: 40%;

    }
    .hero .content h1{
        font-size: 2.75rem;
    }
    .hero .content p{
        font-size: 1.4rem;
    }
}
@media screen and (max-width: 650px){
    .hero .content{
        top: 22%;

    }
    .hero .content h1{
        font-size: 2.75rem;
    }
    .hero .content p{
        font-size: 1.4rem;
    }
        
} 

